import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SecurityListComponent } from './security-list.component';
import { SecurityListRoutingModule } from './security-list-routing.module';
import { MatInputModule, MatButtonModule, MatSelectModule, MatCardModule, MatPaginatorModule, MatTableModule, MatSortModule, MatDialogModule, MatCheckboxModule, MatTooltipModule, MatSlideToggleModule } from '@angular/material';
import { FormsModule, ReactiveFormsModule,  } from '@angular/forms';
import { MatFormFieldModule } from '@angular/material';
import { MatStepperModule } from '@angular/material/stepper';
import {ToasterModule } from 'angular5-toaster';

@NgModule({
  imports: [
    SecurityListRoutingModule,
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    MatStepperModule,
    MatFormFieldModule,
    MatCardModule,
    MatInputModule,
    MatCheckboxModule,
    MatSelectModule,
    MatButtonModule,
    MatPaginatorModule,
    MatDialogModule,
    MatTableModule,
    MatSortModule,
    MatTooltipModule,
    ToasterModule,
    MatSlideToggleModule

  ],
  declarations: [SecurityListComponent]
})
export class SecurityListModule { }



